import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

const WrapDiv = styled.div`
    width: 100%;
    max-width: 1440px;
    padding: 0 130px;
    ${({ theme }) => theme.styles.center}
    margin-bottom: 77px;
    span,
    em {
        width: 100%;
        height: auto;
        font-size: 12px;
        font-weight: 400;
        word-break: keep-all;
        color: ${({ theme }) => theme.colors.gray};
    }
    ul {
        width: 100%;
        height: auto;
        text-align: center;
        line-height: 160%;
    }
    li {
        display: inline-block;
        padding: 0 6px;
    }
    em {
        margin-right: 4px;
    }
    a {
        text-decoration: none;
        color: ${({ theme }) => theme.colors.gray};
    }
`
const CopyrightDiv = styled.div`
    text-align: center;
    span {
        display: block;
        line-height: 160%;
    }
    span:first-child {
        width: 100%;
        height: auto;
        margin-bottom: 50px;
    }
`
const StyledLink = styled(Link)`
    width: 100%;
    height: auto;
    font-size: 12px;
    font-weight: 400;
    word-break: keep-all;
    color: ${({ theme }) => theme.colors.gray};
    text-decoration: none;
`
function FooterBox() {
    return (
        <WrapDiv>
            <CopyrightDiv>
                <span> HEYPOP</span>
                <span>
                          ,  ,
                        .
                </span>
            </CopyrightDiv>
            <ul>
                <li>
                    <span>()</span>
                </li>
                <li>
                    <em></em>
                    <span>   272  5</span>
                </li>
                <li>
                    <em></em>
                    <span></span>
                </li>
                <li>
                    <em></em>
                    <span>637-87-01267</span>
                </li>
                <li>
                    <em></em>
                    <span>
                        <a href="mailto:heypop@design.co.kr" target="blank">
                            heypop@design.co.kr
                        </a>
                    </span>
                </li>
            </ul>
            <ul>
                <li>
                    <StyledLink to={`${process.env.PUBLIC_URL}/about`}>
                         
                    </StyledLink>
                </li>
                <li>
                    <StyledLink to={`${process.env.PUBLIC_URL}/notice`}>
                        
                    </StyledLink>
                </li>
                <li>
                    <StyledLink
                        to={`${process.env.PUBLIC_URL}/terms-conditions`}
                    >
                        
                    </StyledLink>
                </li>
                <li>
                    <StyledLink to={`${process.env.PUBLIC_URL}/privacy-policy`}>
                        
                    </StyledLink>
                </li>
            </ul>
        </WrapDiv>
    )
}
export default FooterBox
